#top {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  #top-bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-position: center;
    background-size: cover;
    &.bg-fixed {
      background-attachment: fixed;
    }
  }

  #top-logo-wrap {
    position: relative;
    text-align: center;
    line-height: 1.1;

    @media (max-width: 1200px) {
      font-size: 0.8em;
    }

    @media (max-width: 600px) {
      font-size: 0.6em;
    }

    #top-logo-img {
      width: 6em;
      margin-bottom: 1em;
      margin-left: -0.8em;
    }

    #top-logo-text {
      text-transform: uppercase;
      font-size: 4.5em;
      font-weight: 600;
      letter-spacing: 0.045em;
      margin-bottom: 0.04em;
    }

    #top-logo-subtitle {
      font-size: 3.04em;
      font-weight: 100;
      margin-left: -0.2em;
    }
  }

  #top-title {
    position: relative;
    width: 100%;

    &.title-404 {
      span {
        font-size: 6em;
        font-weight: 600;
      }
    }

    span {
      font-size: 2em;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: $letter-spacing;
      padding-left: $letter-spacing;
    }
  }

  .arrow {
    position: absolute;
    bottom: 15%;
    margin: 0 auto;
    cursor: pointer;

    svg {
      position: relative;
      top: 0;
      height: 2em;
      transition: 0.3s;
    }

    &:hover {
      svg {
        top: 0.5em;
      }
    }
  }
}
